import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min';
import React from 'react';
import { Awards, Contact, Education, Experience, Home, Log, Media, Organizations, Projects, Skills, Toolbar } from './components/index';
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

function App() {
  return (
    <div className="App">
        <Router>
          <div>
            {/* The Toolbar and Contact feature are accessible no matter the sub URL. */}
            <Toolbar />
            <Contact />
            
            <Routes>
              {/* Different subpages */}
              <Route exact path= {process.env.PUBLIC_URL + "/home"} element = {<Home />} />
              <Route exact path= {process.env.PUBLIC_URL + "/education"} element = {<Education />} />
              <Route exact path= {process.env.PUBLIC_URL + "/skills"} element = {<Skills />} />
              <Route exact path= {process.env.PUBLIC_URL + "/exp"} element = {<Experience />} />
              <Route exact path= {process.env.PUBLIC_URL + "/projs"} element = {<Projects />} />
              <Route exact path= {process.env.PUBLIC_URL + "/orgs"} element = {<Organizations />} />
              <Route exact path= {process.env.PUBLIC_URL + "/awards"} element = {<Awards />} />
              <Route exact path= {process.env.PUBLIC_URL + "/log"} element = {<Log />} />
              <Route exact path= {process.env.PUBLIC_URL + "/"} element = {<Home />} />
            </Routes>

          </div>
        </Router>
    </div> 
  );
}

export default App;
